@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

body {
  padding: 20px;
  min-height: 100vh;
  background-image: url(https://www.oceanprosperityroadmap.org/wp-content/uploads/2015/05/ocean-final.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.App {
  max-width: 320px;
  margin: auto;
}
.List {
  box-shadow: 0 6px 10px rgba(0, 0, 0, .2);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border: 1px solid rgba(255,255,255,.3);
}

.ListHeader {
  display: flex;
  align-items: center;
  height: 60px;
  background-image: linear-gradient(to bottom right, rgba(43, 54, 119, .5), rgba(54, 74, 189, .5));
  padding: 0 20px;
  color: #ffffff;
}

.ListItem {
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  min-height: 40px;
  align-items: center;
  padding: 0 20px;
}

.ListItem span {
  flex-grow: 1;
}

.AddMore {
  background-color: rgba(42, 57, 144, .5);
  padding: 20px;
  display: flex;
}

.AddMore input {
  border: none;
  background-color: rgba(255, 255, 255, .5);
  padding: 7px;
  border-radius: 0;
  flex-grow: 1;
  font-size: 1em;
  height: 40px;
}

.AddMore input::-webkit-input-placeholder {
  color: #222;
}

.AddMore input:-ms-input-placeholder {
  color: #222;
}

.AddMore input::-ms-input-placeholder {
  color: #222;
}

.AddMore input::placeholder {
  color: #222;
}

.AddMore input:focus {
  background-color: #ffffff;
  border-radius: 0;
  outline: none;
}

.AddMore button {
  background-color: rgba(42, 57, 144, .5);
  color: #ffffff;
  padding: 5px 10px;
  font-weight: 400;
  border: none;
  border-radius: 0;
  font-size: 1em;
}
